









import { Component, Vue } from 'vue-property-decorator';

const ModalProps = Vue.extend({
  props: {
    onTitle: String
  }
})

@Component({})
export default class BottomMenu extends ModalProps  {
    visible: boolean = false;
    
    public hide(): void{
        this.visible = false;
    }
    
    public display(): void{
        this.visible = true;
    }

    mounted(): void{
		this.$bus.$on(this.onTitle, this.display);
    }
}
